<template>
  <div class="apply-detail-wrapper">
    <app-header title="审核中" />
    <div class="tip">
      您已成功提交申请，请等候审核人审批，审批通过后，我们将消息通知您
    </div>
    <div class="mt10"></div>
    <Flow :list="list"></Flow>
    <Button class="cancel-btn" v-if="showCancel" @click="doCancel"
      >撤销申请</Button
    >
  </div>
</template>
<script>
import Flow from '@/src/widget/verify-flow';
import Button from '@src/components/app-button';
import { getFlow, cancelApply, getContractFlow } from '@src/api/user-state';

export default {
  components: {
    Flow,
    Button,
  },
  data() {
    return {
      list: [],
      showCancel: false,
      applyId: '', // 申请记录ID
      projectId: '', // 项目ID
      keywordId: '', //合同关键字主键ID
    };
  },

  activated() {
    this.init();
  },
  methods: {
    showError() {
      this.$toast('流程不存在，请联系系统管理员');
      this.$router.go(-1);
    },
    init() {
      this.list = [];
      this.projectId = '';
      this.keywordId = '';
      const { applyId, projectId, keywordId } = {
        ...this.$route.params,
      };

      if (applyId && (projectId || keywordId)) {
        this.applyId = applyId;
        this.showCancel = true;

        this.$toast.loading({
          duration: 0, // 持续展示 toast
          overlay: true,
          forbidClick: true, // 禁用背景点击
          loadingType: 'spinner',
          message: '加载中...',
        });

        if (projectId) {
          this.projectId = projectId;
          this.getProjectFlow(applyId, projectId);
        } else if (keywordId) {
          this.keywordId = keywordId;
          this.getContractFlow(applyId, keywordId);
        }
      } else {
        this.showError();
      }
    },

    // 获取项目的审核流程
    getProjectFlow(applyId, projectId) {
      getFlow({
        apply_id: applyId,
        project_id: projectId,
      })
        .then(data => {
          this.handleFlowData(data);
        })
        .catch(() => {
          this.$toast.clear();
          return this.showError();
        });
    },

    // 获取合同的审核流程
    getContractFlow(applyId, keywordId) {
      getContractFlow({
        apply_id: applyId,
        contract_keyword_id: keywordId,
      })
        .then(data => {
          this.handleFlowData(data);
        })
        .catch(() => {
          this.$toast.clear();
          return this.showError();
        });
    },

    handleFlowData(data) {
      this.$toast.clear();
      console.log('data ======> ', data);
      // 接口返回的data有值时是一个对象，没值时是一个空数组
      if (Array.isArray(data) && !data.length) {
        return this.showError();
      }
      this.list = [];
      this.list.push({
        name: data.apply_username,
        date: data.apply_time,
        status: '发起',
      });

      this.list.push({
        name: data.approve_username,
        date: '',
        status: '待审批',
      });
    },

    doCancel() {
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        overlay: true,
        forbidClick: true, // 禁用背景点击
        loadingType: 'spinner',
        message: '撤销中...',
      });

      const params = {
        apply_id: this.applyId,
      };
      // 若有projectId代表撤销项目
      if (this.projectId) {
        params.project_id = this.projectId;
      } else if (this.keywordId) {
        params.keyword_id = this.keywordId;
      }
      return cancelApply(params)
        .then(() => {
          this.$toast.clear();
          this.$toast('撤销成功');
          this.$router.go(-1);
        })
        .catch(err => {
          console.error('撤销申请失败', this.applyId, err);
          this.$toast.clear();
          this.$toast('撤销失败，请稍后重试');
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.tip {
  margin-top: 2px;
  width: 100%;
  padding: 15px;
  font-size: 14px;
  color: #000000;
  background: #ffffff;
}

.cancel-btn {
  width: 90%;
  margin: 40px auto;
}
</style>
